
function BaseResponse(res){

  let data=null;

  if (res){
    data=res;
  }

  if (data&&typeof(data)=='string')
  {
    try {

      data=JSON.parse(data);
    }
    catch (e) {

    }
  }

  if(data){

    this.res = data.res;

    this.msg = data.msg;

    this.msgData = data.msgData;

    this.origanalRes = data;
  }
  else
  {
    this.res = null;

    this.msg = null;

    this.msgData = null;

    this.origanalRes = null;
  }


  if (data&&parseInt(data.res)==1)
  {
      this.success=true;
  }
  else
  {
      this.success = false;
  }

}

export {BaseResponse}




