<template>
  <div class="container">
    <div class="loginWrap">
      <div class="left">
        <img src="" alt="" class="logo">
        <div class="logoName">龟大师</div>
        <div class="sysName">后台项目管理系统</div>
      </div>
      <div class="right">
        <button class="loginButton" v-on:click="clickLogin" v-bind:disabled="buttonDisabled">{{buttonTitle}}</button>
        <input type="password" class="password" v-model="password" placeholder="密码">
        <input type="text" class="username" v-model="username" placeholder="用户名">
        <div v-if="errorMessage" class="errorMessage">{{errorMessage}}</div>
      </div>
    </div>
  </div>
</template>

<script>

  import {User,storeUser} from "../../user/user";
  import {BaseRequest,sendRequest} from '../../networking/request/oeRequest';
  import {hex_md5} from "../../util/md5";
  export default {
    name: "Login",
    data: function () {
      return {
        username: null,
        password: null,
        errorMessage:null,
        buttonTitle:'登录',
        buttonDisabled:false,
      }
    },
    methods: {
      clickLogin: function () {

        let canLogin=this.verifyCanCommit();

        if (canLogin.result) {

          let username=this.username;

          let password=this.password;

          this.errorMessage=null;

          this.buttonTitle='登录中';

          this.buttonDisabled=true;

          const that=this;

          let request = new BaseRequest('webLogin', {
            phoneno: username,
            password: hex_md5(password),
            type: 1,
          }, null);

          request.success = function (baseResponse) {

            let user = new User(baseResponse.msgData);

            storeUser(user);

            window.location.href = 'admin.html';
          };

          request.fail = function (baseResponse) {

            let title = '登录失败';

            if (baseResponse && baseResponse.msg) {

              title = baseResponse.msg;
            }

            that.errorMessage=title;

            that.buttonTitle='登录';

            that.buttonDisabled=false;

          };

          sendRequest(request);

        } else {

          this.errorMessage=canLogin.message;


        }
      },

      /**
       * 验证是否可以登录
       * @returns {{result: boolean, message: string}}
       */
      verifyCanCommit:function () {

        let result=true;

        let message=null;

        if (!this.username||!this.username.length){
          result=false;
          message='请输入手机/用户名';
        }
        else if (!this.password||!this.password.length) {
          result=false;
          message='请输入密码';
        }

        return {
          result:result,
          message:message
        }

      }
    }
  }

</script>

<style>
  html,body{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 0 0;
    margin: 0 0;
  }
</style>

<style scoped>
  .container {
    width: 100%;
    height: 100%;
    min-width: 1400px;
    min-height: 850px;
    background-size: cover;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background:url("../../assets/login_bg.png") no-repeat;

  }

  .loginWrap{
    width: 720px;
    height: 450px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.50);
  }

  .loginWrap .left{
    width: 430px;
    height: 100%;
    background: #1E1E42;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }

  .loginWrap .right{
    width: 290px;
    height: 100%;
    background: white;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: flex-start;
  }

  .left .logo{
    width: 135px;
    height: 83px;
    margin-top: 117px;
  }

  img[src=""],img:not([src]){opacity:0;}

  .left .logoName{
    height: 28px;
    line-height: 28px;
    font-family: FZLTZHUNHJW--GB1-0;
    font-size: 24px;
    color: #FFFFFF;
    letter-spacing: 5px;
    text-align: center;
    margin-top: 20px;
  }

  .left .sysName{
    height: 21px;
    line-height: 21px;
    font-family: FZLTXHJW--GB1-0;
    font-size: 18px;
    color: #FFFFFF;
    letter-spacing: 1px;
    text-align: center;
    margin-top: 10px;
  }

  .errorMessage{
    width: 190px;
    margin-bottom: 10px;
    display: block;
    min-height: 14px;
    max-height: 60px;
    height: auto;
    height: 14px;
    padding: 8px 0 2px;
    line-height: 14px;
    font-size: 12px;
    color: #fc4343;
    text-align: left;
    overflow: hidden;
  }

  input{
    font-family: PingFangSC-Regular;
    font-size: 14px;
    color: black;
    width: 190px;
    border:0px transparent;
    outline: none;
    position: relative;
    border-bottom: solid 1px #E1E1E1;
    padding-bottom: 3px;
  }

  .username{
    margin-bottom: 10px;

  }

  .password{
    margin-bottom: 75px;

  }

  .loginButton{
    width: 200px;
    height: 38px;
    margin-bottom: 130px;
    background-image: linear-gradient(33deg, #1AC6FA 0%, #0087E7 100%);
    box-shadow: 0 0 5px 0 rgba(0,135,231,0.30);
    border-radius: 19px;
    text-align: center;
    line-height: 38px;
    color: white;
    text-decoration: none;
    font-size: 16px;
    outline: none;
    border: none;
  }


  .loginButton:hover{
    background-image: linear-gradient(33deg, #56CCF2 0%, #0575E6 100%);
    cursor: pointer;
  }

  .loginButton:active{
    background-image: linear-gradient(33deg, #1AC6FA 0%, #0087E7 100%);
  }

  .loginButton[disabled],loginButton[disabled]:hover{
    cursor: default;
    background-image: linear-gradient(33deg, #1AC6FA 0%, #0087E7 100%);
  }

</style>



