
const userInfoKey ='userInfo';

/**
 * 
 * 根据登录返回的用户信息创建User
 * 
 */
export function User(loginResponse){

    if (!loginResponse) {
      loginResponse=new Object();
    }

    this.id=loginResponse.id;
    this.headUrl = loginResponse.headUrl;
    this.phoneno=loginResponse.phoneno;
    this.nickname = loginResponse.nickname;
    this.username = loginResponse.username;
    this.companyName=loginResponse.companyName;
    this.departmentName=loginResponse.departmentName;
    this.webToken=loginResponse.webToken;
    this.companyId=loginResponse.companyId;
    this.departmentId=loginResponse.departmentId;
    //0普通用户 1管理员 2超级管理员 3总经理
    this.type = loginResponse.type;


    if(loginResponse.departmentName!=undefined)
    {
      this.departmentName = loginResponse.departmentName;
    }

    if (loginResponse.departName!=undefined)
    {
      this.departmentName = loginResponse.departName;
    }

    for (var pr in this) {

      if(this[pr]==undefined)
      {
        this[pr]=null;
      }

    }

    if (this.id)
    {
      this.isLogin = true;
    }
    else
    {
      this.isLogin = false;
    }

}

/**
 * 
 * 获取用户的缓存信息
 * 
 */
export function getStoreUser(){

  let userInfo = localStorage.getItem(userInfoKey);

  if (userInfo){

      userInfo=JSON.parse(userInfo);
  }

  let user = new User(userInfo);

  return user;

}


/**
 * 
 * 
 * 缓存用户信息
 * 
 */
export function storeUser(user){

  var value=user;

  if (typeof user !='string'){

    value=JSON.stringify(user);

  }

  localStorage.setItem(userInfoKey,value);

}

export function userIsLogin() {

  let user=getStoreUser();

  if (user&&user.isLogin){

      return true;
  }
  else {
      return false;
  }

}

/**
 * 
 * 清除用户缓存信息
 * 
 */
export function clearUser(){

  localStorage.removeItem(userInfoKey);

}





