
import $ from 'jquery';

import {BaseResponse} from '../response/oeResponse';

import {getStoreUser} from "../../user/user";

var mainHost = 'http://106.14.240.156:9090/opmss/';

//var mainHost = 'http://192.168.2.156:8080/opmss/';


function requestPost(request){

  request.method='POST';

  request.header = {
    'content-type': 'application/x-www-form-urlencoded'
  };

  sendRequest(request);

}

function requestGet(request) {

  request.method = 'GET';

  sendRequest(request);

}

function sendRequest(request){

  var data = request.data;

  if(!data){

    data = new Object();

  }

  const user = getStoreUser();


  if (request.carryUserInfo && user && user.isLogin)
  {
    var userInfo={
      webToken: user.webToken ? user.webToken:null,
      userId: user.id ? user.id:null,
    }

    Object.assign(data, userInfo);

  }

  request.data=data;

  //console.log(request.data);

  $.ajax({
    type: "POST",
    url: request.url,
    data:request.data,
    timeout:request.timeout,
    success: function (res) {
      handleWXResponse(res,request);
      //console.log(res);
    },
    error: function (e) {
      handleWXResponse(null, request);
    }
  })

}

function sendUploadRequest(request) {

  var data = request.data;

  if (!data) {
    data = new Object();
  }

  const user = getStoreUser();

  if (request.carryUserInfo && user && user.isLogin) {
    var userInfo = {
      webToken: user.webToken ? user.webToken : null,
      userId: user.id ? user.id : null,
    }

    Object.assign(data, userInfo);


  }

  request.data=data;


  wx.uploadFile({
    url: request.url,
    filePath: request.filePath,
    name: request.fileKey,
    formData: request.data,
    dataType:'json',
    success: function (res) {
      handleWXResponse(res, request);
      //console.log(res);
    },
    fail: function (res) {
      handleWXResponse(res, request);
      //console.log(res);
    },
    complete: request.complete,
  })
}

/**
 * 
 * 处理调用微信发送请求的响应
 * 
 */
function handleWXResponse(res,request){

  const baseResponse =new BaseResponse(res);


  if (baseResponse.success) {
    if (request.success) {
      request.success(baseResponse);
    }
  }
  else {

    //异地登录
    if(baseResponse.res==110)
    {
      handleLoginOnOtherDevice();
    }

    if (request.fail) {
      request.fail(baseResponse);
    }
  }
}

/**
 * 
 * 
 * 普通网络请求
 * 
 */
function BaseRequest(path, data, host){

    this.path=path;

    this.data=data;

    if (!host||!host.length)
    {
      this.host = mainHost;
    }

    this.url = this.host + (path ? path : '');

    this.method='post';

    this.timeout=30000;

    this.success=null;

    this.fail=null;

    this.complete=null;

    this.header = {
      'content-type': 'application/json'
  };

  this.carryUserInfo=true;

}


/**
 * 
 * 上传网络请求
 * 
 */
function BaseUploadRequest(path, data, host,fileKey,filePath) {

  this.path = path;

  this.data = data;

  if (!host || !host.length) {
    this.host = mainHost;
  }

  this.url = this.host + (path ? path : '');

  this.fileKey=fileKey;

  this.filePath=filePath;

  this.method = 'post';

  this.success = null;

  this.fail = null;

  this.complete = null;

  this.header = {
    'content-type': 'multipart/form-data'
  };

  this.carryUserInfo = true;

}

function handleLoginOnOtherDevice() {

  clearUser();

  window.location.href='../../pages/login.html';
}


export {BaseRequest,sendRequest}




